a:link {
  color: #ff0000;
}

a:visited {
  color: #00ff00;
}

a:hover {
  color: #ff00ff;
}

a:active {
  color: #0000ff;
}

body, html {
  width: 100%;
  height: 100%;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

main .tool {
  margin-top: 50px;
}

main .tool .tool__header {
  font-size: 26px;
  color: #333;
}

main .tool .tool__list {
  list-style: none;
  font-size: 16px;
  line-height: 2;
  padding-left: 0;
}

main .owl-carousel {
  position: relative;
  margin-top: 40px;
}

main .owl-carousel .owl-carousel__img {
  height: 180px;
  background-size: cover;
}

main .owl-carousel .owl-nav .owl-prev, main .owl-carousel .owl-nav .owl-next {
  position: absolute;
  top: 34%;
  display: block;
}

main .owl-carousel .owl-nav .owl-prev {
  left: -20px;
}

main .owl-carousel .owl-nav .owl-next {
  right: -20px;
}

main .owl-carousel .owl-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

main .owl-carousel .owl-dots .owl-dot {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #333;
  margin: 10px;
}

main .owl-carousel .owl-dots .owl-dot.active {
  background-color: red;
}

main .background-section {
  position: relative;
  margin-top: 40px;
  height: 100vh;
  width: 100%;
}

main .background-section .backgroun-img {
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}

main .background-section .backgroun-img:before {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

main .background-section .background-text {
  position: absolute;
  top: 0;
  left: 0;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

main .background-section .background-text .background__title {
  font-size: 30px;
}

main .background-section .background-text .background__description {
  font-size: 18px;
  max-width: 1024px;
}

main .form-container {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

main .form-container .input-group {
  margin-bottom: 30px;
}

main .form-container .input-group .main-label {
  font-size: 18px;
  display: inline-block;
  margin-bottom: 10px;
}

main .form-container .input-group .input-row {
  display: flex;
  justify-content: space-between;
}

#header .header-fixed {
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 100;
  left: 0;
  height: 90px;
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.33);
  background-color: transparent;
  transition: 1s all;
  box-shadow: -1px 6px 14px -2px rgba(0, 0, 0, 0.31);
}

#header .header-fixed.header-fixed__scroll {
  background-color: white;
  border-bottom: 1px solid rgba(102, 102, 102, 0.1);
}

#header .header-fixed.header-fixed__scroll .header-svg {
  margin-right: auto;
}

#header .header-fixed.header-fixed__scroll .header-svg .change-svg-color {
  stroke: #666 !important;
  stroke-width: 3px;
}

#header .header-fixed.header-fixed__scroll .header-fixed__menu .header-fixed__menu-item {
  color: #666;
}

#header .header-fixed.header-fixed__scroll .header-fixed__right-side .header-fixed__social.facebook .facebook-outher {
  fill: #3b5998 !important;
}

#header .header-fixed.header-fixed__scroll .header-fixed__right-side .header-fixed__social.facebook .facebook-inner {
  fill: white;
}

#header .header-fixed.header-fixed__scroll .header-fixed__right-side .header-fixed__social.vk .vk-outher {
  fill: #4c75a3 !important;
  transition: .3s all;
}

#header .header-fixed.header-fixed__scroll .header-fixed__right-side .header-fixed__social.vk .vk-inner {
  fill: white;
}

#header .header-fixed.header-fixed__scroll .header-fixed__right-side .header-fixed__social.mail {
  background-color: #f2a60c;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#header .header-fixed.header-fixed__scroll .header-fixed__right-side .header-fixed__social.mail .email-inner {
  fill: white;
}

#header .header-fixed .header-fixed__left-side {
  flex: 1 1 80%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-left: 30px;
}

@media (max-width: 768px) {
  #header .header-fixed .header-fixed__left-side {
    padding-left: 10px;
    justify-content: center;
  }
}

#header .header-fixed .header-fixed__left-side .header-svg {
  margin-right: auto;
}

#header .header-fixed .header-fixed__left-side .header-svg .change-svg-color {
  fill: white;
  transition: 1s all;
  stroke: black;
}

#header .header-fixed .header-fixed__left-side .header-fixed__city-travel {
  margin-right: auto;
  color: #666;
}

#header .header-fixed .header-fixed__left-side .header-fixed__menu {
  color: white;
  font-size: 14px;
  font-weight: 600;
  list-style: none;
  cursor: pointer;
}

#header .header-fixed .header-fixed__left-side .header-fixed__menu .header-fixed__menu-item {
  display: inline-block;
  transition: 1s all;
  margin-left: 20px;
  margin-right: 20px;
  position: relative;
}

@media (max-width: 768px) {
  #header .header-fixed .header-fixed__left-side .header-fixed__menu .header-fixed__menu-item {
    margin-left: 0;
    margin-right: 5px;
    letter-spacing: .2px;
    font-size: 11px;
  }
}

#header .header-fixed .header-fixed__left-side .header-fixed__menu .header-fixed__menu-item:before {
  content: "";
  display: inline-block;
  background-color: white;
  position: absolute;
  top: 30px;
  width: 0;
  height: 2px;
  transition: .3s all;
}

#header .header-fixed .header-fixed__left-side .header-fixed__menu .header-fixed__menu-item:hover:before {
  width: 100%;
}

#header .header-fixed .header-fixed__right-side {
  flex: 1 1 20%;
  height: 100%;
  border-left: 1px solid rgba(255, 255, 255, 0.33);
}

#header .header-fixed .header-fixed__right-side .header-fixed__list {
  display: flex;
  margin: 0;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social {
  list-style: none;
  cursor: pointer;
}

@media (max-width: 768px) {
  #header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social {
    transform: scale(0.7);
  }
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.facebook .facebook-outher {
  width: 30px;
  height: 30px;
  fill: rgba(0, 0, 0, 0);
  transition: .3s all;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.facebook .facebook-inner {
  fill: white;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.vk .vk-outher {
  width: 30px;
  height: 30px;
  fill: rgba(0, 0, 0, 0);
  transition: .3s all;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.vk .vk-inner {
  fill: white;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.mail {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .3s all;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.mail a {
  display: flex;
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.mail .email-outher {
  width: 18px;
  height: 15px;
  fill: rgba(0, 0, 0, 0);
}

#header .header-fixed .header-fixed__right-side .header-fixed__list .header-fixed__social.mail .email-inner {
  fill: white;
}

#header .header-image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 600px;
  width: 100%;
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
}

#header .header-image .header-image__shadow {
  display: block;
  background: rgba(0, 0, 0, 0.5);
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}

#header .header-image .header-image__main-text {
  z-index: 3;
  text-transform: uppercase;
  font-size: 36px;
  font-weight: 400;
  text-align: center;
  max-width: 800px;
  color: white;
}

@media (max-width: 768px) {
  #header .header-image .header-image__main-text {
    font-size: 24px;
    max-width: 500px;
  }
}

#header .header-image .header-image__button-block {
  position: relative;
  z-index: 4;
  display: flex;
  justify-content: center;
}

@media (max-width: 768px) {
  #header .header-image .header-image__button-block {
    flex-direction: column;
  }
}

#header .header-image .header-image__button-block .header-image__battons--transparent {
  margin-top: 30px;
  margin-right: 20px;
  display: block;
  width: 200px;
  border: 1px solid white;
  color: white;
  text-decoration: none;
  padding: 10px;
  cursor: pointer;
  transition: .3s all;
}

#header .header-image .header-image__button-block .header-image__battons--transparent:hover {
  background-color: white;
  color: #666;
  border-radius: 4px;
}

#header .header-image .header-image__button-block .header-image__battons--white {
  margin-top: 30px;
  display: block;
  width: 200px;
  border: 1px solid white;
  color: #666;
  text-decoration: none;
  padding: 10px;
  cursor: pointer;
  transition: .3s all;
  background-color: white;
}

#header .header-image .header-image__button-block .header-image__battons--white:hover {
  background-color: transparent;
  color: white;
  border-radius: 4px;
}
