#header {
  .header-fixed {
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    z-index: 100;
    left: 0;
    height: 90px;
    width: 100%;
    border-bottom: 1px solid rgba(255, 255, 255, 0.33);
    background-color: transparent;
    transition: 1s all;
    box-shadow: -1px 6px 14px -2px rgba(0,0,0,0.31);

    // CLASSES FOR HEADER FIXED WHEN SCROLL
    &.header-fixed__scroll {
      background-color: white;
      border-bottom: 1px solid rgba(102, 102, 102, 0.10);

      .header-svg{
        margin-right: auto;
        .change-svg-color{
          stroke: #666 !important;
          stroke-width: 3px;
        }

      }

      .header-fixed__menu {
        .header-fixed__menu-item{
          color: #666;
        }
      }

      .header-fixed__right-side{
        .header-fixed__social{
          &.facebook {
            .facebook-outher{
              fill: #3b5998 !important;
            }
            .facebook-inner{
              fill: white;
            }
          }
          &.vk {
            .vk-outher{
              fill: #4c75a3 !important;
              transition: .3s all;
            }
            .vk-inner{
              fill: white;
            }
          }
          &.mail{
            background-color: #f2a60c;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            .email-inner{
              fill: white;
            }
          }
        }
      }
    }

    // CLASSES FOR HEADER FIXED WHEN SCROLL END
    .header-fixed__left-side {
      flex: 1 1 80%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      padding-left: 30px;


      @media (max-width:768px){
        padding-left: 10px;
        justify-content: center;


      }

      .header-svg{
        margin-right: auto;

        .change-svg-color{
          fill: white;
          transition: 1s all;
          stroke: black;
        }
      }

      .header-fixed__city-travel{
        margin-right: auto;
        color:#666;
      }


      .header-fixed__menu {
        color: white;
        font-size: 14px;
        font-weight: 600;
        list-style: none;
        cursor: pointer;

        .header-fixed__menu-item{
          display: inline-block;
          transition: 1s all;
          margin-left: 20px;
          margin-right: 20px;
          position: relative;

          @media (max-width:768px){
            margin-left: 0;
            margin-right: 5px;
            letter-spacing: .2px;
            font-size: 11px;

          }

          &:before{
            content: "";
            display: inline-block;
            background-color: white;
            position: absolute;
            top:30px;
            width:0;
            height: 2px;
            transition:.3s all;
          }

          &:hover {
            &:before{
              width:100%;

            }
          }
        }
      }
    }
    .header-fixed__right-side {
      flex: 1 1 20%;
      height: 100%;
      border-left: 1px solid rgba(255, 255, 255, 0.33);

      .header-fixed__list{
        display: flex;
        margin: 0;
        justify-content: space-around;
        align-items: center;
        height: 100%;
        .header-fixed__social{
          list-style: none;
          cursor: pointer;

          @media (max-width:768px) {
            transform: scale(0.7);
          }

          &.facebook {
            .facebook-outher{
              width: 30px;
              height: 30px;
              fill: rgba(0,0,0,0);
              transition: .3s all;
            }

            .facebook-inner{
              fill: white;
            }
          }

          &.vk {
            .vk-outher{
              width: 30px;
              height: 30px;
              fill: rgba(0,0,0,0);
              transition: .3s all;
            }

            .vk-inner{
              fill: white;
            }
          }

          &.mail{

            a{
              display: flex;
            }
            width: 30px;
            height: 30px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .3s all;

            .email-outher{
              width: 18px;
              height: 15px;
              fill: rgba(0,0,0,0);

            }

            .email-inner{
              fill: white;
            }
          }
        }
      }
    }
  }
  .header-image {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 600px;
    width: 100%;
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    z-index: 1;

    .header-image__shadow {
      display: block;
      background: rgba(0,0,0,0.5);
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      position: absolute;

    }

    .header-image__main-text{
      z-index: 3;
      text-transform: uppercase;
      font-size: 36px;
      font-weight: 400;
      text-align: center;
      max-width: 800px;
      color: white;

      @media (max-width:768px) {
        font-size: 24px;
        max-width: 500px;
      }
    }


    .header-image__button-block {
      position: relative;
      z-index: 4;
      display: flex;
      justify-content: center;

      @media (max-width:768px) {
        flex-direction: column;
      }

      .header-image__battons--transparent{

        margin-top: 30px;
        margin-right: 20px;
        display: block;
        width: 200px;
        border: 1px solid white;
        color:white;
        text-decoration: none;
        padding: 10px;
        cursor: pointer;
        transition: .3s all;

        &:hover {
          background-color: white;
          color: #666;
          border-radius: 4px;
        }
      }

      .header-image__battons--white{
        margin-top: 30px;
        display: block;
        width: 200px;
        border: 1px solid white;
        color: #666;
        text-decoration: none;
        padding: 10px;
        cursor: pointer;
        transition: .3s all;
        background-color: white;

        &:hover {
          background-color: transparent;
          color: white;
          border-radius: 4px;
        }
      }
    }
  }
}