body,html{
  width: 100%;
  height: 100%;

}
.container{
  max-width: 1024px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

main {

  .tool{
    margin-top: 50px;

    .tool__header{
      font-size: 26px;
      color: #333;
    }
    .tool__list{
      list-style: none;
      font-size: 16px;
      line-height: 2;
      padding-left: 0;
    }
  }
  // owl-carousel
  .owl-carousel{
    position: relative;
    margin-top: 40px;
    .owl-carousel__img {
      height: 180px;
      background-size: cover;
    }
    .owl-nav{
      .owl-prev, .owl-next {
        position: absolute;
        top:34%;
        display: block;
      }

      .owl-prev{
        left: -20px;
      }
      .owl-next{
          right:-20px;
      }
    }

    .owl-dots {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 20px;
      .owl-dot {
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background-color: #333;
        margin: 10px;
        &.active {
          background-color: red;
        }
      }
    }
  }

  // section background
  .background-section {
    position: relative;
    margin-top: 40px;
    height: 100vh;
    width: 100%;

    .backgroun-img{
      background-attachment: fixed;
      position: absolute;
      top:0;
      left: 0;
      height: 100vh;
      background-size: cover;
      background-repeat: no-repeat;
      width: 100%;

          &:before {
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, .5);
          }
    }

    .background-text {
      position: absolute;
      top:0;
      left: 0;
      color:white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100vh;


      .background__title{
        font-size: 30px;
      }
      .background__description{
        font-size: 18px;
        max-width: 1024px;
      }

    }
  }

  // form
  .form-container{
    margin-top:50px;
    display:flex;
    justify-content: center;

    .input-group{
      margin-bottom: 30px;
      .main-label{
        font-size: 18px;
        display: inline-block;
        margin-bottom: 10px;
      }
      .input-row {
        display: flex;
        justify-content: space-between;

        .input-row__wrapper{


        }
      }
    }
  }

}